// import google font
@if variable-exists(import-google-font-1) {
    @import url($import-google-font-1);
}

@if variable-exists(import-google-font-2) {
    @import url($import-google-font-2);
}

@if variable-exists(import-google-font-3) {
    @import url($import-google-font-3);
}

// @name Html
html {
    box-sizing: border-box;
    font-size: 62.5%;
}

*,
*::before,
*::after {
    box-sizing: inherit;
    color: inherit;
}

// @name Body
body {
    font-family: $typo-1;
    font-weight: $normal;
    overflow-x: hidden;

    &.-overflow-hidden {
        overflow: hidden;
    }

    a {
        text-decoration: none;

        &:focus,
        &:active {
            outline: 1px dashed $color-focus-outline;
            outline-offset: 2px;
        }
    }
}

input,
textarea,
select,
button {
    &:focus {
        outline: 1px dashed $color-focus-outline;
        outline-offset: 2px;
    }
}

// hide SVG symbol
.svg-symbol {
    display: none;
}

// Temp
.test-wrapper {
    min-height: 12rem;
    padding: 0 0 1rem;
    position: relative;
}
