    text-align: center;
    margin: 0 auto;
    font-weight: normal;
    font-size: 1em;
    width: 100%;
    text-decoration: none;
    padding-top: 0.3em;
    text-transform: uppercase;
    vertical-align: middle;
    cursor: pointer;
}

/*
