    created elements which results in one http request being made for each
    cell of each table rendered by the script.
    */
    _background-image: none;
}

.date-picker table {
    table-layout: auto;
    empty-cells: show;
    border-spacing: 2px;
    border-collapse: separate;
    position: relative;
    margin: 0;
    padding: 0;
    border: 1px solid #ccc;
    background-position: 0 -20px;
    text-align: center;
    padding: 0.3em;
    width: auto;
    height: auto;
    color: #555;
    font-size: 1em;
    border-radius: 0.8em;
    box-shadow: 0px 0px 5px #aaa;
}

/*
    Create a "Lifted Corners" effect on the table. Ripped straight from @necolas
    http://nicolasgallagher.com/css-drop-shadows-without-images/
*/
.date-picker:before,
.date-picker:after {
    content: "";
    position: absolute;
    z-index: -2;
    bottom: 17px;
    left: 10px;
    width: 50%;
    height: 20%;
    max-width: 300px;
    max-height: 100px;
    -webkit-box-shadow: 0 15px 10px rgba(0, 0, 0, 0.7);
    -moz-box-shadow: 0 15px 10px rgba(0, 0, 0, 0.7);
    box-shadow: 0 15px 10px rgba(0, 0, 0, 0.7);
    -webkit-transform: rotate(-3deg);
    -moz-transform: rotate(-3deg);
    -ms-transform: rotate(-3deg);
    -o-transform: rotate(-3deg);
    transform: rotate(-3deg);
}

.date-picker:after {
    right: 10px;
    left: auto;
    -webkit-transform: rotate(3deg);
    -moz-transform: rotate(3deg);
    -ms-transform: rotate(3deg);
    -o-transform: rotate(3deg);
    transform: rotate(3deg);
}

.oldie .date-picker:before,
.oldie .date-picker:after {
    display: none;
}

/*
    Common TD & TH styling
*/
.date-picker td,
.date-picker tbody th {
    padding: 0;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    background-position: 0 -35px;
    width: 3em;
    height: 3em;
    overflow: hidden;
    outline: transparent none 0px;
    border: 1px solid #cfcfcf;
    text-transform: none;
    font-weight: normal;
    text-shadow: 0 1px #fff;
    border-radius: 3px;
}

.date-picker th {
    border: 0 none;
    padding: 0;
    font-weight: bold;
    color: #333;
    text-align: center;
    vertical-align: middle;
    text-transform: none;
}

.date-picker .date-picker-week-header {
    border: 1px solid #dcdcdc;
    font-style: oblique;
    background: transparent;
    cursor: default;
}

.date-picker .date-picker-thead .date-picker-week-header {
    border: 0 none;
    cursor: help;
}

/*
    The "mon tue wed etc" day header styles
*/
.date-picker .date-picker-day-header {
    cursor: help;
    border: 0 none;
    padding: 0 0 0.2em 0;
    text-transform: lowercase;
    height: auto;
}

/*
    tfoot status bar
*/
.date-picker .date-picker-statusbar {
    cursor: default;
    font-weight: normal;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    border: 0 none;
    background: #fff;
    height: 2.8em;
}

/*
    TD cell that is _not_ used to display a day of the month
*/
.date-picker .date-picker-unused {
    border-color: #dcdcdc;
    cursor: default !important;
    background-position: 0 -25px;
}

/*
    The TH cell used to display the "month, year" title
*/
.date-picker .date-picker-title {
    width: auto;
    height: auto;
    padding: 0.4em 0;
}

/*
    The "todays date" cell style
*/
.date-picker .date-picker-today {
    background: #fff url(data:image/gif;base64,) no-repeat 0 0;
}

.oldie .date-picker .date-picker-today {
    *background: #fff url("../images/datepicker/datepicker-today.gif") no-repeat 0 0;
}

.date-picker .month-out.date-picker-highlight {
    color: #b87676;
}

/*
    The "highlight days" style
*/
.date-picker .date-picker-highlight {
    color: #a86666;
}

/*
    The "active cursor" style
*/
.date-picker td {
    /*

    Uncomment this if you want a transition on the cursor cells scale effect

    -webkit-transition: all .2s ease;
       -moz-transition: all .2s ease;
        -ms-transition: all .2s ease;
         -o-transition: all .2s ease;
            transition: all .2s ease;
    */
}

/*
    Keyboard users get a scaled cursor. webKit exhibits buggy behaviour when
    scaling TD cells so commented out until I find a curious CSS based fix.
    Opera no longer appears to apply the scale... ho hum...
*/
.date-picker-focus .date-picker-hover {
    /*
    -webkit-transform:scale(1.25);
    */
    -moz-transform: scale(1.25);
    -ms-transform: scale(1.25);
    -o-transform: scale(1.25);
    transform: scale(1.25);
}

.date-picker .date-picker-hover,
.date-picker .month-out.date-picker-hover {
    background: #fff url(data:image/jpg;base64,) no-repeat 0 0;
    cursor: pointer;
    border-color: #6482aa !important;
    border-color: rgba(100, 130, 170, 0.7) !important;
    color: #6482aa;
    text-shadow: 0px 1px 1px #fff;
    box-shadow: 0 0 6px rgba(100, 130, 170, 0.6);
    background-size: cover;
}

.oldie .date-picker .date-picker-hover,
.oldie .date-picker .month-out.date-picker-hover {
    *background: #fff url("../images/datepicker/datepicker-cursor.jpg") no-repeat 0 0;
}

/*
    The "disabled days" style
*/
.date-picker .day-disabled {
    color: #888;
    cursor: default;
    text-decoration: line-through;
    background-position: 0 -15px;
}

.date-picker .month-out {
    border-color: #ddd;
    border-color: rgba(220, 220, 220, 0.8);
    color: #aaa;
    background-position: 0 -25px;
}

/*
    The "selected date" style - color changed below also
*/
.date-picker .date-picker-selected-date {
    border-color: #888 !important;
    border-color: rgba(7, 7, 7, 0.6) !important;
}

/*
    The date "out of range" style
*/
.date-picker .out-of-range,
.date-picker .not-selectable {
    color: #ccc;
    font-style: oblique;
    background: #fcfcfc;
    cursor: default;
}

/*
    Week number "out of range" && "month-out" styles
*/
.date-picker th.month-out,
.date-picker th.out-of-range {
    color: #aaa;
    font-style: oblique;
    background: #fcfcfc;
}

/*
    Week numbers "out of range"
*/
.date-picker .out-of-range {
    opacity: 0.6;
}

/*
    Used when the entire grid is full but the next/prev months dates cannot be
    selected
*/
.date-picker .not-selectable {
    opacity: 0.8;
}

.oldie .date-picker .out-of-range {
    filter: alpha(opacity=60);
}

.oldie .date-picker .not-selectable {
    filter: alpha(opacity=80);
}

.date-picker tr {
    display: table-row;
}

.date-picker sup {
    font-size: 0.86em;
    letter-spacing: normal;
    text-transform: none;
    height: 0;
    line-height: 1;
    position: relative;
    top: -0.2em;
    vertical-align: baseline !important;
    vertical-align: top;
}

.date-picker .date-picker-day-header,
.date-picker .month-display,
.date-picker .year-display {
    text-shadow: 0px 1px 1px #fff;
}

.date-picker .month-display,
.date-picker .year-display {
    cursor: default;
}

.date-picker td:focus,
.date-picker .date-picker-hover {
    overflow: hidden;
    -moz-outline: 0 none;
    outline: 0 none;
    -o-highlight: 0 none;
}

