    The "selected date" style
*/
.date-picker .date-picker-selected-date {
    color: #333;
    color: rgba(3, 3, 3, 0.8);
    font-weight: bold;
    font-size: 1.2em;
    width: 2.4em;
    height: 2.4em;
}

