.sr-only {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto;
}

/* stylelint-enable */
html {
    box-sizing: border-box;
    font-size: 62.5%;
}

*,
*::before,
*::after {
    box-sizing: inherit;
    color: inherit;
}

body {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    overflow-x: hidden;
}

body.-overflow-hidden {
    overflow: hidden;
}

body a {
    text-decoration: none;
}

body a:focus, body a:active {
    outline: 1px dashed #ca000a;
    outline-offset: 2px;
}

input:focus,
textarea:focus,
select:focus,
button:focus {
    outline: 1px dashed #ca000a;
    outline-offset: 2px;
}

.svg-symbol {
    display: none;
}

.test-wrapper {
    min-height: 12rem;
    padding: 0 0 1rem;
    position: relative;
}

.ce-align-left {
    text-align: left;
}

.ce-align-right {
    text-align: right;
}

.ce-align-center {
    text-align: center;
}

.ce-table td,
.ce-table th {
    vertical-align: top;
}

.ce-textpic,
.ce-nowrap .ce-bodytext {
    overflow: inherit;
}

