    .ce-left .ce-gallery,
    .ce-column {
        float: left;
    }
}

.ce-center .ce-outer,
.ce-center .ce-inner {
    float: right;
    position: relative;
}

.ce-center .ce-outer {
    right: 50%;
}

.ce-center .ce-inner {
    right: -50%;
}

.ce-gallery {
    overflow: inherit;
