    display: table;
    margin: 0;
}

.ce-gallery figcaption {
    caption-side: bottom;
    color: #575c5e;
    display: table-caption;
    font-family: "Poppins", sans-serif;
    font-size: 1.3rem;
    font-weight: 300;
    padding-top: 1.6rem;
}

.ce-gallery .copyright {
    display: block;
    font-size: 1.2rem;
