.btn {
    transition: all 250ms ease-in-out 0ms;
    background-color: transparent;
    border: solid 1px #212851;
    color: #212851;
    display: inline-flex;
    justify-content: center;
    font-family: "Poppins", sans-serif;
    line-height: 1.14286;
    text-decoration: none;
    width: auto;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 1.4rem;
    padding: 1.1rem 2.1rem;
    font-weight: 400;
}

.btn[data-fa-icon]::before, .btn::before, .btn::after {
    transition: all 250ms ease-in-out 0ms;
    color: #212851;
}

.btn.-icon::before {
    content: "\f0d9";
}

.btn.-icon::before {
    color: #212851;
    font-size: 1em;
    font-weight: 700;
    line-height: 1;
    margin-right: 0.25em;
}

.btn.-icon-r {
    display: inline-flex;
    flex-direction: row-reverse;
}

.btn.-icon-r:before {
    margin-left: 1rem;
    font-weight: 300;
}

.btn.-no-text, .btn.-circle {
    overflow: hidden;
    position: relative;
    text-align: left;
    text-indent: -9999px;
    white-space: nowrap;
}

.btn.-no-text::before, .btn.-no-text::after, .btn.-circle::before, .btn.-circle::after {
    display: block;
    left: 50%;
    margin: 0;
    position: absolute;
    text-indent: 0;
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
}

@media screen and (max-width: 1279px) {
    .btn.-no-text-mobile {
        height: 4rem;
        width: 4rem;
        overflow: hidden;
        position: relative;
        text-align: left;
        text-indent: -9999px;
        white-space: nowrap;
    }
    .btn.-no-text-mobile::before, .btn.-no-text-mobile::after {
        display: block;
        left: 50%;
        margin: 0;
        position: absolute;
        text-indent: 0;
        top: 50%;
        transform: translateY(-50%) translateX(-50%);
    }
}

.btn.-circle, .btn.-no-text {
    padding: 1.2rem 1.6rem;
}

.btn:hover, .btn:focus {
    background-color: #212851;
    border-color: transparent;
    color: #fff;
    cursor: pointer;
    text-decoration: none;
}

.btn:hover[data-fa-icon]::before, .btn:hover::before, .btn:hover::after, .btn:focus[data-fa-icon]::before, .btn:focus::before, .btn:focus::after {
    color: #fff;
}

