 *
 * @section Click-roll
 * @page Components
 *
 */
.click-roll {
    border-top: 2px solid transparent;
    margin: 6.8rem 0;
    position: relative;
}

@media screen and (max-width: 767px) {
    .click-roll {
        margin: 5.7rem 0;
    }
}

.click-roll + .click-roll {
    margin-top: -6.8rem;
}

@media screen and (max-width: 767px) {
    .click-roll + .click-roll {
        margin-top: -5.7rem;
    }
    .faq-block .click-roll + .click-roll {
        margin-top: 0;
    }
}

.click-roll__toggle {
    transition: all 250ms ease-in-out 0ms;
    background-color: #dfe4e7;
    border: 0;
    color: #212851;
    cursor: pointer;
    display: block;
    font-size: 1.9rem;
    font-weight: 700;
    padding: 1em 3.5em 1em 1.5em;
    position: relative;
    text-align: left;
    width: 100%;
    line-height: 1.4;
}

@media screen and (max-width: 767px) {
    .click-roll__toggle {
        line-height: 1;
        padding: 1rem 6rem 1rem 1rem;
    }
}

.click-roll__toggle::before {
    content: '';
    content: '';
    width: 6.2rem;
    background: #e8d000;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
}

@media screen and (max-width: 767px) {
    .click-roll__toggle::before {
        width: 4.5rem;
    }
}

.click-roll__toggle::after {
    content: "\f063";
}

.click-roll__toggle::after {
    position: absolute;
    color: #212851;
    top: 2.2rem;
    right: 2.2rem;
    transition: 0s;
    transform: rotate(0deg);
}

@media screen and (max-width: 767px) {
    .click-roll__toggle::after {
        right: 1.5rem;
    }
}

.click-roll__toggle:focus {
    outline: 1px dashed #ca000a;
    outline-offset: 2px;
}

.click-roll__toggle:hover, .click-roll__toggle:focus,
.-is-open .click-roll__toggle {
    background-color: #79c7c3;
    color: #212851;
}

.click-roll__toggle:hover::after, .click-roll__toggle:focus::after,
.-is-open .click-roll__toggle::after {
    color: #212851;
}

.click-roll__toggle:hover:before, .click-roll__toggle:focus:before,
.-is-open .click-roll__toggle:before {
    background: #33b4a7;
}

.click-roll__block {
    display: none;
    font-size: 1.7rem;
    overflow: hidden;
    z-index: 5;
}

@media screen and (max-width: 767px) {
    .click-roll__block {
        font-size: 1.6rem;
    }
}

.click-roll__slide-wrap {
    padding: 1.2em 1.8em;
}

@media screen and (max-width: 767px) {
    .click-roll__slide-wrap {
        padding: .8em 1.4em;
        line-height: 1.6;
    }
}

.click-roll__slide-wrap img {
    max-width: 100%;
}

.click-roll__slide-wrap p {
    line-height: 1.7;
}

.click-roll.-is-open .click-roll__toggle::after {
    transform: rotate(180deg);
}

.click-roll.-is-open .click-roll__block {
    display: block;
}

