    right: 0.7rem;
}

.lang__current {
    background-color: rgba(0, 0, 0, 0.8);
    border: 0;
    color: inherit;
    font-family: inherit;
    font-size: 1.3rem;
    font-weight: 300;
    line-height: 1;
    padding: 1.35rem 1.6rem 1.35rem 1rem;
    position: relative;
    transition: all 300ms ease-in-out 0ms;
}

.lang__current:hover, .lang__current:focus {
    background-color: rgba(0, 0, 0, 0.65);
}

.lang__current:focus {
    outline-offset: 0;
}

.lang__current abbr {
    text-decoration: none;
}

.lang__drop {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.8);
    margin: 0;
    padding: 0;
    width: 100%;
    z-index: 5;
