$height: 50rem;
$tablet-height: 38rem;

:root {
    --base: #212851;
    --bg-blend: screen;
    --blur: 0px;
    --fg-blend: multiply;
    --foreground: #79c7c3;
    --opacity: 1;
    --spacing: 0%;
  }

.page-image {
    position: relative;
    display: block;
    width: 100%;
    overflow: hidden;

    &:before,
    &:after {
        position:absolute;
        top:0;
        left:0;
        width: 100%;
        height: 357px;
        z-index: 2;
        background: linear-gradient(to bottom, rgba(33,40,81,0.91) 0%,rgba(33,40,81,0.78) 19%,rgba(33,40,81,0.66) 31%,rgba(33,40,81,0.17) 77%,rgba(33,40,81,0.09) 86%,rgba(33,40,81,0) 100%);
        content: '';
    }

    &:after {
        top: auto;
        height: 250px;
        bottom: 0;
        background: linear-gradient(to bottom, rgba(33,40,81,0) 0%,rgba(33,40,81,0.09) 14%,rgba(33,40,81,0.17) 23%,rgba(33,40,81,0.66) 69%,rgba(33,40,81,0.78) 81%,rgba(33,40,81,0.91) 100%);
    }

    &__background {
        display: block;
        width: 100%;
        background-color: var(--base);
        position: relative;

        &::before {
            background-color: var(--foreground);
            bottom: 0;
            content: '';
            height: 100%;
            left: 0;
            mix-blend-mode: var(--fg-blend);
            position: absolute;
            right: 0;
            top: 0;
            width: 100%;
            z-index: 1;
        }

        img {
            @include size(100%);
            display: block;
            filter: grayscale(100%) contrast(1) blur(var(--blur));
            flex: 1 0 100%;
            height: 100%;
            max-width: 100%;
            mix-blend-mode: var(--bg-blend);
            object-fit: cover;
            opacity: var(--opacity);
            position: relative;
            width: 100%;
        }

        &.-static-height {
            img {
                font-family: 'object-fit: cover; object-position: center;';
                height: $height;
                object-fit: cover;

                @include breakpoint(medium down) {
                    height: $tablet-height;
                }

                @include breakpoint(small down) {
                    height: auto;
                }
            }
        }
    }
}
