$btn-size: 6;
$btn-size-mobile: 6;

.go-to-top {
    position:absolute;
    bottom: 20.2rem;
    right: 3rem;
    display: block;
    opacity: 1;
    visibility: visible;
    z-index: 51;

    @include breakpoint(medium down) {
        bottom: auto;
        right: 2rem;
        top: -3rem;
    }

    @include breakpoint(small down) {
        right: 1rem;
        top: -2.7rem;
    }

    p {
        margin: 0;
        position: absolute;
        right: 0;
        text-align: right;
    }

    a {
        @include size(#{$btn-size}rem);
        @include hide-text-with-icon();
        @include icon-before($fa-var-angle-up);
        @include transition(all, 400ms, ease, 0ms);
        background-color: $color-3--5;
        display: block;
        overflow: hidden;
        text-indent: 9999px;
        border-radius: 50%;

        &::before {
            color: $color-1--3;
            font-size: 1.6rem;
            font-weight: $bold;
            left: 50%;
            line-height: 6rem;
            position: absolute;
            transform: translateX(-50%);
        }

        @include on-event() {
            background-color: $color-2--3;
            &:before {
                color: $color-white;
            }
        }
    }

    &.-is-hide {
        opacity: 0;
        transition: opacity 0.5s ease;
        visibility: hidden;
    }
}
