$hero-height: 93.4rem;
$hero-height-full: 100vh;
$active-color: $color-2--1;

.hero {
    $this: &;
    height: auto;
    position: relative;
    width: 100%;
    z-index: 0;

    &:before,
    &:after {
        position:absolute;
        top:0;
        left:0;
        width: 100%;
        height: 357px;
        z-index: 2;
        background: linear-gradient(to bottom, rgba(33,40,81,0.91) 0%,rgba(33,40,81,0.78) 19%,rgba(33,40,81,0.66) 31%,rgba(33,40,81,0.17) 77%,rgba(33,40,81,0.09) 86%,rgba(33,40,81,0) 100%);
        content: '';
    }

    &:after {
        top: auto;
        height: 250px;
        bottom: 0;
        background: linear-gradient(to bottom, rgba(33,40,81,0) 0%,rgba(33,40,81,0.09) 14%,rgba(33,40,81,0.17) 23%,rgba(33,40,81,0.66) 69%,rgba(33,40,81,0.78) 81%,rgba(33,40,81,0.91) 100%);
    }

    @include breakpoint(medium down) {
        &:before {
            height: 242px;
        }
        &:after {
            height: 183px;
        }
    }

    @include breakpoint(small down) {
        &:before {
            height: 118px;
        }
        &:after {
            height: 129px;
        }
    }

    &-slider {
        position: relative;
        &:before {
            position:absolute;
            bottom: -6.1rem;
            left:0;
            width: 23.1rem;
            height: 38.6rem;
            background: url(../images/hero-decor.png) no-repeat;
            z-index: 3;
            content: '';
            @include breakpoint(medium down) {
                width: 18.6rem;
                height: 31.7rem;
                background-size: 100% auto;
                bottom: -5.4rem;
            }
            @include breakpoint(small down) {
                width: 7.5rem;
                height: 14.4rem;
                bottom: 3.6rem;
            }
        }
    }

    /*&::after {
        @include absolute(null, null, 0, 0);
        @include bg-pattern($color-white, 0.2);
        @include size(100%, 27.7rem);
        content: '';

        @include breakpoint(medium down) {
            content: none;
        }
    }*/

    &-form {
        @include absolute(null, null, 17.5rem, 50%);
        transform: translateX(-50%);
        width: 82rem;
        z-index: 2;

        @include breakpoint(medium down) {
            display: none;
        }

        &__title {
            color: $color-white;
            font-family: $typo-2;
            font-size: 10rem;
            font-weight: $black;
            line-height: 61px;
            margin: 0;
            padding-left: 4rem;
            text-align: center;

            &::before {
                content: '#';
            }
        }

        form {
            @include flex();
            align-items: center;
            border: 10px solid rgba(255, 255, 255, 0.4);
            border-radius: 10px;
            height: 9rem;
            margin: 0;
        }

        &__input {
            height: 100%;
            width: 100%;

            input {
                background: rgba($color-white, 0.95);
                box-sizing: border-box;
                color: $color-3--4;
                display: block;
                font-size: 2rem;
                font-style: normal;
                font-weight: $light;
                height: 100%;
                padding: 2.25rem 1.5rem 2.25rem 2.5rem;

                &::placeholder {
                    color: $color-3--4;
                    font-size: 2rem;
                    font-style: normal;
                    font-weight: $light;
                }
            }
        }

        &__btn {
            @include size(7.2rem, 100%);
            @include trs();
            background: rgba($color-white, 0.95);
            border: transparent;
            color: $color-1--3;
            cursor: pointer;
            flex-shrink: 0;
            font-size: 2rem;
            font-weight: $normal;
            position: relative;

            i {
                font-weight: $normal;
            }

            @include on-event() {
                background-color: $color-1--3;
                color: $color-white;
            }

            &::before {
                @include absolute(null, null, 0, 0);
                @include size(0.8rem, 100%);
                background: transparent url('#{$image-assets}/search-shadow.png') no-repeat center bottom;
                content: '';
                opacity: 0.7;
            }
        }
    }

    &-quick {
        @include flex(null, wrap, center, center, null);
        margin-top: 1rem;
        z-index: 2;

        &__link {
            @include trs();
            background-color: $color-black;
            color: $color-white;
            display: inline-block;
            font-size: 1.6rem;
            font-weight: $light;
            margin: 0 0.5rem 1rem;
            overflow: hidden;
            padding: 1.2rem 2rem;
            position: relative;

            &::before {
                @include trs();
                color: $color-1--2;
                content: '#';
                margin-right: 0.3rem;
            }

            @include on-event() {
                background-color: $color-1--3;

                &::before {
                    color: $color-white;
                }
            }
        }
    }
}

.hero-slider {
    &.-static-height {
        height: $hero-height;

        @include breakpoint(medium down) {
            height: auto;
        }
    }
}

:root {
  --base: #212851;
  --bg-blend: screen;
  --blur: 0px;
  --fg-blend: multiply;
  --foreground: #79c7c3;
  --opacity: 1;
  --spacing: 0%;
}

.hero-item {
    $this: &;
    display: block;
    height: 100%;
    position: relative;

    @include breakpoint(medium down) {
        height: auto;
    }


    &__image {
        display: block;
        height: 100%;
        background-color: var(--base);
        display: flex;
        flex: 1 1 100%;
        height: 100%;
        overflow: hidden;
        padding: var(--spacing);
        position: relative;

        &::before {
            background-color: var(--foreground);
            bottom: 0;
            content: '';
            height: 100%;
            left: 0;
            mix-blend-mode: var(--fg-blend);
            position: absolute;
            right: 0;
            top: 0;
            width: 100%;
            z-index: 1;
        }

        @include breakpoint(medium down) {
            height: auto;
        }

        img {
            @include size(100%);
            display: block;
            filter: grayscale(100%) contrast(1) blur(var(--blur));
            flex: 1 0 100%;
            height: 100%;
            max-width: 100%;
            mix-blend-mode: var(--bg-blend);
            object-fit: cover;
            opacity: var(--opacity);
            position: relative;
            width: 100%;

            @include breakpoint(medium down) {
                height: auto;
            }
        }
    }
}
