.see-again {
	display: -ms-grid;
	display: grid;
	-ms-grid-columns: 1fr 1fr 1fr 1fr 1fr;
	-ms-grid-rows: 1fr 1fr 1fr;
	grid-template-columns: repeat(5, 1fr);
	width: 100%;
	padding-top: 7rem;
	padding-bottom: 5rem;
	@include breakpoint(medium down) {
		display: flex;
		flex-wrap: wrap;
		padding-bottom: 2rem;
	}
	@include breakpoint(small down) {
		padding-top: 3.7rem;
	}
	&-col-3 {
		grid-column: span 3;
	}
	&-col-2 {
		grid-column: span 2;
	}
	&-row-2{
		grid-row: span 2;
	}
	[class*='see-again-item'] {
		&:nth-child(2) {
			-ms-grid-row: 1;
			-ms-grid-row-span: 1;
			-ms-grid-column: 4;
			-ms-grid-column-span: 1;
		}
		&:nth-child(3) {
			-ms-grid-row: 1;
			-ms-grid-row-span: 1;
			-ms-grid-column: 5;
			-ms-grid-column-span: 1;
		}
		&:nth-child(4) {
			-ms-grid-row: 2;
			-ms-grid-row-span: 1;
			-ms-grid-column: 1;
			-ms-grid-column-span: 1;
		}
		&:nth-child(5) {
			-ms-grid-row: 2;
			-ms-grid-row-span: 2;
			-ms-grid-column: 2;
			-ms-grid-column-span: 2;
		}
		&:nth-child(6) {
			-ms-grid-row: 2;
			-ms-grid-row-span: 1;
			-ms-grid-column: 4;
			-ms-grid-column-span: 1;
		}
		&:nth-child(7) {
			-ms-grid-row: 2;
			-ms-grid-row-span: 1;
			-ms-grid-column: 5;
			-ms-grid-column-span: 1;
		}
		&:nth-child(8) {
			-ms-grid-row: 3;
			-ms-grid-row-span: 1;
			-ms-grid-column: 1;
			-ms-grid-column-span: 1;
		}
		&:nth-child(9) {
			-ms-grid-row: 3;
			-ms-grid-row-span: 1;
			-ms-grid-column: 4;
			-ms-grid-column-span: 1;
		}
		&:nth-child(10) {
			-ms-grid-row: 3;
			-ms-grid-row-span: 1;
			-ms-grid-column: 5;
			-ms-grid-column-span: 1;
		}
	}
	&-item {
		position: relative;
		overflow: hidden;
		&-wp {
			padding-top: 73.5%;
		}
		&-title {
			display: flex;
			align-items: center;
			padding: 20px 4.6% 0 20%;
			-ms-grid-row: 1;
			-ms-grid-row-span: 1;
			-ms-grid-column: 1;
			-ms-grid-column-span: 3;
			.title-block {
				width: 100%;
				padding-right: 33%;
			}
		}
		&-btn {
			display: flex;
			align-items: center;
			justify-content: center;
		}
		@include on-event() {
			.see-again-content {
				opacity: 1;
			}
		}
		@include breakpoint(medium down) {
			padding-top: 0;
			width: calc(50% - 3rem);
			padding: 0;
			margin: 0 1rem 2rem;
			&-wp {
				padding-top: 0;
			}
			&:nth-child(2n) {
				margin-left: 2rem;
			}
			&:nth-child(2n+1) {
				margin-right: 2rem;
			}
			&-title {
				width: 100%;
				padding: 0 2rem;
				.title-block {
					padding: 0;
				}
			}
			&-btn {
				width: 100%;
				.links-group {
					padding: 0.7rem 0;
				}
			}
		}
		@include breakpoint(small down) {
			margin: 0 1rem 2rem;
			width: calc(100% - 2rem);
			&:nth-child(2n),
			&:nth-child(2n+1) {
				margin: 0 1rem 2rem;
			}
			&-title {
				padding: 0 1rem;
			}
			&-btn {
				.links-group {
					padding: 0;
					.btn {
						margin: 0;
					}
				}
			}
		}
	}
	&__img {
		img {
			position:absolute;
			top:50%;
			left:50%;
			transform: translate(-50%, -50%);
			min-width: 101%;
			min-height: 101%;
			max-width: none;
			width: auto;
		}
		@include breakpoint(medium down) {
			position: relative;
			display: block;
			img {
				position: static;
				min-width: 0;
				min-height: 0;
				width: 100%;
				display: block;
				transform: translate(0, 0);
			}
		}
	}
	&-content {
		position:absolute;
		top:0;
		left:0;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: center;
		text-align: center;
		background: rgba(33,40,81,0.8);
		z-index: 3;
		transition: all 0.3s linear;
		padding: 2rem 11%;
		opacity: 0;
		@include breakpoint(medium down) {
			opacity: 1;
			position: static;
			height: auto;
			background: none;
			padding: 1.9rem 2rem 0;
		}
		@include breakpoint(small down) {
			padding: 0.8rem 3rem 0;
		}
	}
	&-category {
		font-size: 1.4rem;
		line-height: 1.2;
		display: block;
		font-weight: $bold;
		color: $color-2--1;
		margin-bottom: 1.8rem;
		@include breakpoint(medium down) {
			color: $color-2--5;
			margin-bottom: 1.3rem;
		}
		@include breakpoint(small down) {
			margin-bottom: 1rem;
		}
	}
	&-title {
		font-size: 1.8rem;
		line-height: 1.1;
		color: $color-white;
		padding-bottom: 1.2rem;
		font-weight: $bold;
		display: block;
		width: 100%;
		&:after {
			display: block;
			margin: 2.2rem auto 0;
			width: 2.5rem;
			height: 0.1rem;
			background: $color-3--2;
			content: '';
		}
		a {
			&:before {
				position:absolute;
				top:0;
				left:0;
				width: 100%;
				height: 100%;
				z-index: 2;
				content: '';
			}
		}
		@include breakpoint(medium down) {
			color: $color-1--3;
			font-size: 2rem;
			padding-bottom: 1.4rem;
			&:after {
				background: $color-2--1;
				margin: 1.1rem auto 0;
			}
		}
		@include breakpoint(small down) {
			font-size: 1.8rem;
			padding-bottom: 1.2rem;
			&:after {
				margin: 1.3rem auto 0;
			}
		}
	}
	&-info {
		display: block;
		font-size: 1.4rem;
		line-height: 1.2;
		font-weight: $bold;
		color: $color-white;
		@include breakpoint(medium down) {
			color: $color-1--3;
		}
	}
	&-play {
		width: 6.3rem;
		height: 6.3rem;
		background: rgba(121,199,195,0.9);
		border-radius: 50%;
		position:absolute;
		top:50%;
		left:50%;
		transform: translate(-50%, -50%);
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 2;
		i {
			font-size: 1.8rem;
			line-height: 1;
			color: $color-1--3;
			margin-left: 0.4rem;
		}
	}
}