/**
 * @section Infos without columns
 * @page Infos
 *
 * @example
 * <div class="infos">
 *      <p class="infos__item -address">
 *          <span class="ghost">Adresse : </span>
 *          13 Lorem ipsum dolor sit<br>amert 25697 loreum ipsuli
 *      </p>
 *      <p class="infos__item -website -underline"><a href="#">Site internet</p>
 *      <p class="infos__item -phone">
 *          <span class="ghost">Tl : </span>
 *          <a href="tel:0494000000">04 94 00 00 00</a>
 *      </p>
 *      <p class="infos__item -fax">
 *          <span class="ghost">Fax : </span>
 *          <a href="tel:0494000000">04 94 00 00 00</a>
 *      </p>
 *      <p class="infos__item -email -underline"><a href="mailto:name@domain.com">Courriel</a></p>
 *      <p class="infos__item -info">
 *          <span class="ghost">Informations : </span>
 *          Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aliquid error minus numquam officia saepe vitae.
 *      </p>
 *      <p class="infos__item -hours">
 *          <span class="ghost">Horaires : </span>
 *          Lundi : 15h-18h / Mardi : 9h30-12h30 / Jeudi : 9h30-12h30 / 14h30-18h30
 *      </p>
 * </div>
 */

.infos {
    $this: &;

    &__wrap {
        display: flex;
        margin: 3rem 0 2rem;

        @include breakpoint(medium down) {
            flex-direction: column;
            margin: 2rem 0 3rem;
        }
    }

    &__items {
        flex-basis: 50%;
        flex-grow: 1;
        flex-shrink: 1;
        padding-right: 5%;

        @include breakpoint(medium down) {
            padding: 0 0 1rem;
        }

        &.-has-border {
            border-left: 1px solid $color-3--3;
            padding: 0 0 0 5%;

            @include breakpoint(medium down) {
                border-left: 0;
                border-top: 1px solid $color-3--3;
                padding: 1rem 0 0;
            }
        }
    }

    &__item {
        color: $color-black;
        font-family: $typo-1;
        font-size: 1.5rem;
        font-weight: $light;
        margin: 0.7rem 0;
        padding-left: 2rem;
        position: relative;

        a {
            color: inherit;
            text-decoration: underline;

            @include on-event() {
                text-decoration: none;
            }
        }

        &::before {
            color: $color-1--3;
            left: 0;
            position: absolute;
            top: 0;
        }

        @each $class, $icon in (
            'address': $fa-var-map-marker-alt,
            'website': $fa-var-globe,
            'phone': $fa-var-phone,
            'mobile': $fa-var-mobile-android-alt,
            'fax': $fa-var-fax,
            'email': $fa-var-at,
            'envelope': $fa-var-envelope,
            'info': $fa-var-info-circle,
            'hours': $fa-var-clock,
            'facebook': $fa-var-facebook-f,
            'twitter': $fa-var-twitter,
            'instagram': $fa-var-instagram,
            'linkedin': $fa-var-linkedin-in,
            'renseignement': $fa-var-comments,
            'route': $fa-var-location-arrow,
            'contact': $fa-var-users,
            'manager': $fa-var-briefcase,
            'deadlines': $fa-var-clock,
            'budget': $fa-var-euro-sign,
            'consequences': $fa-var-info-circle,
            'place': $fa-var-exclamation-triangle
        ) {
            &.-#{$class} {
                @include icon-before(#{$icon});
            }
        }

        &.-facebook,
        &.-twitter,
        &.-instagram,
        &.-linkedin {
            &::before {
                font-family: $font-awesome-brands !important;
            }
        }

        &.-renseignement {
            margin-top: 3rem;
        }

        &.-public,
        &.-name {
            padding-left: 0;
        }
    }

    &.-single {
        #{$this}__wrap {
            @include breakpoint(medium down) {
                margin: 0.5rem 0 0;
            }
        }
    }
}
